* {
  font-size: 15pt;
}

.main-container {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 1.5em;
  font-family: 'Open Sans', sans-serif;
}

.header {
  min-height: 10em;
  padding-bottom: 0.5em;
  margin: 0 1em;
  text-align: center;
}

.logo {
  height: 10em;
  max-width: 100%;
  display: inline-block;
  align-self: center;
}

.title {
  font-size: 1.5em;
  font-weight: bold;
}

.subtitle {
  font-size: 1.1em;
}

.section {
  text-overflow: ellipsis;
  overflow: hidden;
}

.content {
  border-color: #DC9024;
  border-style: solid;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-left: 0;
  border-right: 0;
  padding-bottom: 1em;
}

.footer {
  color: #BBB;
  font-size: 0.7em;
  text-align: center;
  padding: 1em 0;
  margin-top: 1em;
  width: 100%;
  height: 2em;
}

.movie-small {
  width: 49.6%;
  height: 56.25vw;
  padding: 0;
  margin: 0;
}

.movie-right {
  float: right;
}

.icon {
  width: 1.2em;
  height: 1.2em;
  color: black;
  display: inline-block;
  vertical-align: middle;
}

